@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

body {
  margin: 0;
  padding: 0; }

.demos {
  font-family: 'Roboto',  'Helvetica Neue Light', 'Open Sans', Helvetica;
  display: flex;
  color: #353C44; }
  .demos nav {
    width: 350px;
    min-height: 1200px;
    box-sizing: border-box;
    border-right: 1px solid #CBDCE4; }
    .demos nav .logo-container {
      padding: 20px 40px;
      height: 20px;
      border-bottom: 1px solid #CBDCE4;
      display: flex; }
      .demos nav .logo-container a {
        line-height: 27px; }
      .demos nav .logo-container b, .demos nav .logo-container a {
        margin-right: 10px; }
    .demos nav ul {
      list-style-type: none;
      padding: 0;
      box-sizing: border-box; }
      .demos nav ul a {
        color: #53585B;
        display: block;
        padding: 10px 0 10px 40px;
        text-decoration: none; }
        .demos nav ul a .menu-button {
          display: flex; }
          .demos nav ul a .menu-button .menu-icon {
            opacity: 0.6;
            width: 40px; }
          .demos nav ul a .menu-button .menu-button-inner {
            width: 185px; }
        .demos nav ul a.active {
          pointer-events: none;
          padding: 10px 0 10px 36px;
          border-left: 4px solid #353C44;
          color: #353C44; }
          .demos nav ul a.active .menu-button .menu-icon {
            opacity: 1; }
  .demos main {
    width: calc(100% - 350px);
    box-sizing: border-box; }
    .demos main .simulator-content {
      background: #ECF0F4;
      padding: 28px 80px 60px; }
      .demos main .simulator-content h1 {
        margin: 0 0 30px 20px;
        font-size: 22px; }
      .demos main .simulator-content .ka {
        border-radius: 12px;
        box-shadow: 5px 15px 30px rgba(0, 0, 0, 0.1); }
      .demos main .simulator-content .ka-tbody {
        max-height: 600px; }
    .demos main .code {
      padding: 60px 80px; }

.button {
  cursor: pointer;
  opacity: 0.5; }
  .button:hover {
    opacity: 1; }

.custom-cell-image {
  width: 40px;
  height: 40px;
  object-fit: cover;
  border-radius: 25px; }

@font-face {
  font-family: 'icons';
  src: url(/ka-table/static/media/icons.423d006d.eot);
  src: url(/ka-table/static/media/icons.423d006d.eot) format("embedded-opentype"), url(/ka-table/static/media/icons.ab3d38bf.woff2) format("woff2"), url(/ka-table/static/media/icons.c03dc181.woff) format("woff"), url(/ka-table/static/media/icons.74411c9c.ttf) format("truetype"), url(/ka-table/static/media/icons.b9d51453.svg) format("svg");
  font-weight: normal;
  font-style: normal; }

.ka-icon-group-arrow-collapsed:before {
  content: '\e800'; }

.ka-icon-close:before {
  content: '\e801'; }

.ka-icon-group-arrow-expanded:before {
  content: '\e802'; }

.ka-icon-sort-arrow-down:before {
  content: '\e803'; }

.ka-icon-sort-arrow-up:before {
  content: '\e804'; }

.ka {
  overflow: hidden;
  background-color: white; }

.ka-table {
  font-size: 14px;
  width: 100%;
  border-collapse: collapse;
  height: 100%; }

.ka-thead {
  background-color: #F1F5F7; }

.ka-thead-cell {
  padding: 15px 20px;
  color: #747D86; }

.ka-tbody {
  display: block;
  overflow-y: overlay;
  height: 100%; }

.ka-thead-cell, .ka-cell {
  text-align: left; }

.ka-cell {
  padding: 8px 20px;
  line-height: 29px;
  color: #353C44; }

.ka-icon {
  font-weight: 100;
  font-size: 10px;
  font-family: 'icons'; }

.ka-input {
  max-width: 100%; }

.ka-cell-editor-validation-error .ka-input {
  background: #FFE7E7;
  border: 1px solid #FF0C0C; }

.ka-validation-message-container {
  position: absolute; }

.ka-validation-message {
  font-size: 12px;
  color: white;
  background-color: #FF0C0C;
  padding: 0 10px;
  max-width: 200px; }

.ka-empty-cell {
  width: 40px;
  min-width: 40px; }

.ka-tr {
  display: table;
  width: 100%;
  table-layout: fixed; }

.ka-row {
  box-sizing: border-box;
  border-bottom: 1px solid #F9FBFC;
  border-top: 1px solid #F9FBFC; }

.ka-row-selected {
  background-color: #F7FcFd; }

.ka-group-row {
  background-color: #F9FBFC;
  box-sizing: border-box;
  border-bottom: 1px solid white;
  border-top: 1px solid white; }

.ka-group-column {
  padding: 8px 10px; }

.ka-group-column-content {
  display: flex;
  align-items: center; }

.ka-icon-group-arrow {
  padding: 10px 5px 10px 10px;
  margin-right: 5px;
  cursor: pointer; }

.ka-icon-sort {
  margin-left: 3px; }

.ka-pointer {
  cursor: pointer; }

.ka-filter-row-cell {
  padding: 0 20px 15px 20px; }

.editor-links {
  text-align: right;
  font-size: 14px;
  color: #868A8F; }
  .editor-links .editor-link-ts {
    margin: 0 10px; }
  .editor-links .editor-link-js {
    margin-left: 10px; }

.top-element {
  margin-bottom: 20px; }

